import { IUpdatable, ISceneObject } from "../scene/IScene";

export class DamageableAI implements IUpdatable {

    protected readonly _obj: ISceneObject;
    protected readonly _destroyedCallback: () => void;
    protected _hitpoints: number;

    public constructor(controlledObject: ISceneObject, hitpoints: number, destroyedCallback: () => void) {
        this._obj = controlledObject;
        this._hitpoints = hitpoints;
        this._destroyedCallback = destroyedCallback;

        this._obj.element.addEventListener("hit", (e: Event) => this._hit((e as CustomEvent<number>).detail));
    }

    protected _hit(damage: number): void {
        if (this._hitpoints <= 0)
            return;
            
        this._hitpoints -= damage;

        this._obj.element.classList.add("hit");
        setTimeout(() => {
            this._obj.element.classList.remove("hit");

            if (this._hitpoints <= 0)
                this._die();            
        }, 100);
    }

    protected _die() {
        this._destroyedCallback();
    }

    public update(diff: number): void {
    }
}